import React from 'react';
import { Card } from 'antd';
import { ItemPanel, Item } from 'gg-editor';
import StartNode from "../shape/nodes/startnode";
import NormalNode from "../shape/nodes/normalnode";
import EndNode from "../shape/nodes/endnode";
import './index.css';

const FlowItemPanel = () => {
  return (
    <ItemPanel className="itemPanel">
      <Card bordered={false}>
        <Item
          type="node"
          size="40*40"
          shape="start-node"
          model={{
            color: '#FA8C16',
            label: 'Start',
          }}
          src="data:image/svg+xml;base64,"
        />
        <Item
          type="node"
          size="80*48"
          shape="normal-node"
          model={{
            color: '#1890FF',
            label: 'Select Roles',
          }}
          src="data:image/svg+xml;base64,"
        />
        {/*<Item
          type="node"
          size="80*72"
          shape="flow-rhombus"
          model={{
            color: '#13C2C2',
            label: 'Decision',
          }}
          src="data:image/svg+xml;base64,"
        />*/}
        <Item
          type="node"
          size="80*48"
          shape = "end-node"
          model={{
            color: '#722ED1',
            label: 'End',
          }}
          src="data:image/svg+xml;base64,"
        />
        <StartNode></StartNode>
        <NormalNode></NormalNode>
        <EndNode></EndNode>
      </Card>
    </ItemPanel>
  );
};

export default FlowItemPanel;
